.footerContainer{
    background-color: rgb(170, 0, 250);
    display: grid;
    grid-template-columns: 33% 33% 33%;
    padding: 10px;
}
.footerItems{
    color: white;
    font-size: 20px;
}
.footerItems a{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.815);
}

@media (max-width:768px) {
    .footerContainer{
        grid-template-columns: auto;
    }
}